import * as React from 'react';
import styles from './carousel.module.css';
import { useSprings, animated } from 'react-spring';
import { useDrag } from 'react-use-gesture';
import { Box } from '@chakra-ui/core';

function clamp(num, clamp, higher) {
  return higher ? Math.min(Math.max(num, clamp), higher) : Math.min(num, clamp);
}

export default function Carousel({ activeIndex = 0, auto = 0, children = [] }) {
  const sliderRef = React.useRef();
  const [slide, setSlide] = React.useState(0);
  const [isDragging, setDragging] = React.useState(false);

  // Initialize slides with spring
  const [springProps, setSpringProps] = useSprings(children.length, (index) => ({
    offset: index,
  }));

  const bind = useDrag(({ down, movement: [xDelta], direction: [xDir], distance, cancel, first }) => {
    if (first) {
      setDragging(true);
    }
    if (sliderRef && sliderRef.current && sliderRef.current.parentElement) {
      const { width } = sliderRef.current.parentElement.getBoundingClientRect();

      if (down && distance > width / 2) {
        if (cancel) cancel();
        setSlide((slide + (xDir > 0 ? -1 : 1)) % children.length);
      }
      setSpringProps((index) => ({
        offset: (down ? xDelta : 0) / width + (index - slide),
      }));
    }
  });

  // Triggered on slide change
  React.useEffect(() => {
    setSpringProps((index) => ({ offset: index - slide }));
  }, [slide, setSpringProps]);

  // Effect for autosliding
  React.useEffect(() => {
    let interval;

    if (auto > 0) {
      interval = setInterval(() => {
        const targetIndex = (slide + 1) % children.length;
        setSlide(targetIndex);
      }, auto);
    }

    return () => {
      if (interval) clearInterval(interval);
    };
  }, [auto, children.length, slide]);

  // Jump to slide index when prop changes
  React.useEffect(() => {
    setSlide(activeIndex % children.length);
  }, [activeIndex, children.length]);

  const nextSlide = () => {
    setSlide((slide + 1) & children.length);
  };

  const previousSlide = () => {
    setSlide((slide - 1) & children.length);
  };

  return (
    <Box h="100%" w="100%" ref={sliderRef}>
      <Box position="relative" overflow="hidden" h="100%" w="100%">
        {/* {hasArrows && (
          <StyledSliderArrows>
            <Arrow ArrowComponent={ArrowComponent} arrowStyle={arrowStyle} direction="left" onClick={previousSlide} />
            <Arrow ArrowComponent={ArrowComponent} arrowStyle={arrowStyle} direction="right" onClick={nextSlide} />
          </StyledSliderArrows>
        )}
        {hasBullets && (
          <StyledBullets>
            <StyledBulletList>
              {children.map((_, index) => (
                <Bullet
                  key={index} // eslint-disable-line react/no-array-index-key
                  index={index}
                  BulletComponent={BulletComponent}
                  setSlide={setSlide}
                  activeIndex={slide}
                  bulletStyle={bulletStyle}
                />
              ))}
            </StyledBulletList>
          </StyledBullets>
        )} */}
        {springProps.map(({ offset }, index) => (
          <animated.div
            {...bind()}
            key={index}
            style={{
              transform: offset.interpolate((offsetX) => `translate3d(${offsetX * 100}%, 0, 0)`),
              position: 'absolute',
              width: '100%',
              height: '100%',
              willChange: 'transform',
            }}
          >
            <Box h="100%" w="100%">
              {children[index]}
            </Box>
          </animated.div>
        ))}
      </Box>
    </Box>
  );
}
